.intro{

margin: auto;

display: flex;
margin-top: 15vh;
width: 100%;
   height: 70vh;

 text-align: center;
 align-items: center;

    
}
.intro_left{flex:1;
    display: flex;
padding: 2%;
flex-direction: column;
align-items: center;
text-align: center;}

.hi{
    font-size: 2.3rem;
}
.my_name{font-size: 1.9rem;}
.dev{font-size: 1.4rem;}

.logo_box{

    display: flex;
margin-top: 10%;
     width: 60%;
     gap: 4%;
 text-align: center;
    justify-content: center;
}

.logo_box>div{
    height: 50px;

    align-items: center;
    justify-content: center;
    width: 50px;
  background-color:#05e438;
    border-radius: 50%;
  
}
.logo_box>div:hover{
cursor: pointer;
}
.logo_box>div>img{
    width: 60%;
display: block;
margin: auto;
margin-top: 20%;

}
.in{border-radius: 10%;}

.resume{background-color:#05e438;
width: 140%;
margin-left: 20%;
margin-top: 9%;
align-self: center;
height: 60%;
color: rgb(6, 6, 59);
border-radius: 2.3rem;
font-weight: 600;}


.intro_right{flex:1;
    display: flex;
justify-content: center;
align-items: center;
text-align: center;}

.my_logo{width: 45%;
border-radius: 1.5rem;
    border: 8px solid rgb(6, 6, 59);
}

@media all and (min-width:550px) and  (max-width:907px) {

.intro{margin-top: 2vh;}
    .intro_left{
        margin-top: 7%;
        
    }


 



    .my_logo{width: 60%;
    margin-bottom: 17%;}

    .hi{
        font-size: 160%;
    }
    .my_name{font-size: 1.3rem;}
    .dev{font-size: 1.2rem;}

  }

  @media all and (min-width:0px) and  (max-width:550px) {
.intro{flex-direction: column;
    height: 60vh;
  
    margin-bottom: 30%;
}

.logo_box{
    width: 80%;
   margin-right: 15%;
}
.logo_box>div{
    height: 40px;
    width: 40px;
}
.hi{
    font-size: 160%;
}
.my_name{font-size: 1.3rem;}
.dev{font-size: 1.2rem;}


.my_logo{
    display: block;
width: 50%;
margin-top: 5%;
margin-bottom: 20%;}

  }